:root {
  --font_color: #fff;
  --bg_color: #222;
}

@keyframes colorful {
  0% {
    color: #fff;
    filter: blur(1px) hue-rotate(0deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff, 0 0 25px #00b3ff, 0 0 30px #00b3ff;
  }
  100% {
    color: #fff;
    filter: blur(1px) hue-rotate(360deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff;
  }
}

/* layout */
* {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 1.5rem;
  background-color: var(--bg_color);
  color: var(--font_color);
  box-sizing: border-box;
}

li {
  list-style: none;
}
a {
  color: var(--font_color);
  text-decoration: none;
}

.inner {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}

/* header */
#header {
  position: fixed;
  width: 100%;
  height: 90px;
  border-bottom: 1px solid rgba(100, 100, 100, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#header span {
  padding: 0 20px;
  animation: colorful 4s linear infinite;
}
#header span a {
  font-size: 2rem;
}

#header .menu li {
  float: left;
  width: 125px;
  padding: 8px;
  text-align: center;
  font-weight: bold;
}

/* section 공통 */
section {
  padding-top: 90px;
}

/* ./components/menu/Home */
#Home {
  width: 100%;
  height: 85vh;
}
#Home .inner {
  position: relative;
  width: 1280px;
  height: 100%;
}
#Home .inner div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  top: calc(50% - 25vh);
}
#Home div .sparkling {
  position: relative;
  height: 80%;
  width: 50%;
}
#Home div .sparkling img {
  width: 500px;
  height: 500px;
}

#Home .home_text {
  width: 50%;
  height: 100%;
}
#Home .home_text h2 {
  font-size: 5rem;
  display: block;
}
#Home .home_text p {
  margin-top: 12px;
  line-height: 77px;
}
#Home .home_text p span {
  font-size: 2.5rem;
  color: #222;
  background: #fff;
  padding: 3px 50px;
  font-weight: bold;
}

/* ./components/menu/Resume */
#resume {
  width: 100%;
  height: 100%;
}
#resume * {
  color: #222;
}
#resume .inner {
  width: 90%;
  height: 70vh;
  margin: 0 auto;
  position: relative;
}
#resume .inner .resume_wrap {
  position: absolute;
  top: calc(50% - 27.5vh);
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 150px 20% 150px 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#resume .inner .resume_wrap .main {
  width: 97.5%;
  height: 95%;
  border: 5px solid black;
  border-style: dashed;
  border-radius: 150px 20% 150px 15%;
  position: relative;
  display: flex;
}
#resume .inner .resume_wrap .main > div {
  padding-top: 25px;
}
#resume .main hr {
  border: 2px solid;
}
#resume .main p,
#resume .main li {
  font-size: 22px;
  font-weight: normal;
}
#resume .main .L_main {
  width: 35%;
  height: 100%;
}
#resume .main .L_main figure {
  width: 100%;
  height: 50%;
  padding-left: 100px;
}
#resume .main .L_main figure img {
  width: 300px;
  height: 100%;
  border-radius: 50%;
}
#resume .main .L_main div {
  padding-left: 50px;
  margin-top: 1.5rem;
  line-height: 50px;
}
#resume .main .M_main {
  width: 15%;
  height: 100%;
}

#resume .main .R_main {
  width: 50%;
  height: 100%;
}
#resume .main .R_main div {
  width: 70%;
  margin-left: 50px;
  margin-bottom: 50px;
}

#resume .main .R_main div ul li {
  list-style: circle;
}
#resume .main .R_main div:last-child ul {
  margin-top: 1rem;
}
#resume .main .R_main div:last-child ul li {
  list-style: none;
  width: 50px;
  height: 50px;
  float: left;
}

/* ./components/menu/projects (swiper)*/
#projects{
  width:100%;
  height:75vh;
}

#projects .swiper {
  width: 100%;
  height: 100%;
}

#projects .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  color:#222;
  transform: scale(0.8);
  opacity: 0.8;
}
#projects .swiper-slide-active{
  transform: scale(1);
  opacity: 1;
}
#projects .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* ./components/menu/Contact */
#contact {
  width: 100%;
  height: 100%;
}
#contact *{
  color:#222;
}
#contact .card_wrap{
  width:600px;
  height:600px;
  background: #fff;
  margin:0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  margin-top:5%;
}
#contact .card_wrap .card{
  width:95%;
  height:95%;
  border:2px dashed;
  border-radius: 50px;
  position: relative;
}

#contact .card>div{
  width:100%;
  height: 100%;
  text-align: center;
  line-height: 70px;
}
#contact .card>div h2{
  width:200px;
  margin:0 auto;
  border-bottom:2px solid #222;
  margin-bottom:50px;
}
#contact .card>div li{
  cursor: pointer;
}


/* ./components/Footer */
#footer * {
  cursor: default;
}
#footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(100, 100, 100, 0.5);
}
#footer > span {
  padding-left: 2rem;
}
#footer ul li {
  font-size: 2rem;
  cursor: pointer;
}
#footer ul li svg {
  cursor: pointer;
}

#footer p {
  padding-right: 2rem;
}

#footer p span {
  animation: colorful 2s linear infinite;
}
